import React, { Component } from 'react'
import Input from './Input'
import List from './List'

class TodoList extends Component {
    //构造函数
    constructor(props){
        super(props);
        this.state={
            inpValue:'',
            todos:[]
        }
    }
     //组件挂载
    componentDidMount(){
        let todos=localStorage.getItem('todos');
        if(todos){
            this.setState({
                todos:JSON.parse(todos)
            })
        }
    }
     //是否更新
    componentDidUpdate=()=>{
        //本地存储
        localStorage.setItem('todos',JSON.stringify(this.state.todos));
        return true;
    }
    //数组添加
    addTodo=()=>{
        if(this.state.inpValue==''){
            return;
        }
        this.setState(
            {
                todos:[
                    {
                        title:this.state.inpValue,
                        done:false
                    },
                    ...this.state.todos
                ]
            }
        )
        console.log(this.state.inpValue);  
        this.state.inpValue='';   
    }
    //数组删除
    delTodo=(idx)=>{
        this.setState({
            todos:this.state.todos.filter((item,index)=>index!=idx)
        })
    }
    //表单输入
    handleChange=(e)=>{
        this.setState({inpValue:e.target.value});
    }
    //check切换
    toggle=(index)=>{
            let newTodos=JSON.parse(JSON.stringify(this.state.todos));
            newTodos[index].done=!newTodos[index].done;
            this.setState({todos:newTodos});
    }
    //渲染
    render() {
        const {inpValue,todos}=this.state;
        return (
            <div>
                <Input value={inpValue} onChange={this.handleChange} addTodo={this.addTodo}/>
                <List value={inpValue} delTodo={this.delTodo} toggle={this.toggle} todos={todos}/>
            </div>
        )
    }
}

export default TodoList;