import React from 'react'

let List = (props)=>{
  let arr1=props.todos.filter(item=>item.done);
  let arr2=props.todos.filter(item=>!item.done);
  return <div>
    <h2>正在进行{arr2.length}</h2>
    <ul>
      {
        props.todos.map((item,index)=>{
          if(!item.done){
            return <li key={index}>
                <input type="checkbox" checked={item.done} onChange={()=>props.toggle(index)}/>
                <span>{item.title}</span>
                <button onClick={()=>props.delTodo(index)}>删除</button>
            </li>
          }
        })
      }
    </ul>
    <h2>已经完成{arr1.length}</h2>
    <ul>
      {
        props.todos.map((item,index)=>{
          if(item.done){
            return <li key={index}>
                <input type="checkbox" checked={item.done} onChange={()=>props.toggle(index)}/>
                <span>{item.title}</span>
                <button onClick={()=>props.delTodo(index)}>删除</button>
            </li>
          }
        })
      }
    </ul>
  </div>
}
export default List;